.flex-box {
	display: flex;

	&.align-cnt {
		align-items: center;
	}

	&.justify-cnt {
		justify-content: center;
	}

	&.dir-col {
		flex-direction: column;
	}
}

button {
	cursor: pointer;
}

button,
input,
select {
	height: 50px;
	border-radius: 4px;
	border: 1px solid #ddd;
	padding: 0 10px;
}